<!--
 * @Descripttion: vxe-table
 * @version:
 * @Author: YZZ
 * @Date: 2020-03-26 15:43:07
 * @LastEditors: ZJM
 * @LastEditTime: 2022-09-22 09:45:47
 -->
<template>
  <div>
    <vxe-grid
      class="vxe-table-element"
      :ref="tableName+'Table'"
      :row-style="rowStyle"
      :header-row-style="headerStyle"
      :resizable="resizable"
      :border="border"
      :show-header="showHeader"
      :auto-resize="autoResize"
      :header-align="headerAlign"
      :show-footer="showFooter"
      :show-footer-overflow="showFooterOverflow"
      :show-overflow="showOverflow"
      :stripe="stripe"
      :max-height="maxHeight"
      :height="height"
      :size="size"
      :remote-sort="true"
      :highlight-hover-row="highlightHoverRow"
      :highlight-current-row="highlightCurrentRow"
      :loading="tableLoading"
      :tooltip-config="tooltipConfig"
      :keep-source="keepSource"
      :edit-config="editConfig"
      :columns="tableColumns"
      :data="tableDatas"
      :checkbox-config="checkboxConfig"
      :radio-config="{trigger: 'row'}"
      :seq-config="{startIndex: (tablePage.currentPage - 1) * tablePage.pageSize}"
      :pager-config="tablePage"
      :span-method="mergeRowMethod"
      :export-config="{ cellRender: handleCellRender }"
      @cell-click="cellClickEvent"
      @cell-dblclick="cellDBLClickEvent"
      @page-change="handlePageChange"
      @sort-change="sortChange"
      @checkbox-change="checkboxChangeEvent"
      @checkbox-all="checkboxChangeEvent"
      @current-change="currentChangeEvent"
    >
      <!-- pager_left-->
      <template v-slot:pager_left >
        <div class="page-left">
          <!-- vxe-pagervxe 1448 -->
          <!-- <div class="tableAllCheck vxe-pager">
            <vxe-checkbox
              v-model="isAllChecked"
              :indeterminate="isIndeterminate"
              @change="changeAllEvent"
            ></vxe-checkbox>
          </div>
          <div>
            <span class="select-count"> {{ selectRecords.length }} </span>
          </div> -->
          <div>
            <slot name="tableOperation"></slot>
          </div>
        </div>
      </template>
    </vxe-grid>
    <!-- <h2>{{tablePage}}</h2> -->
  </div>
</template>

<script>
export default {
  props: {
    rowStyle: {
      type: Function
    },
    headerStyle: {
      type: Function
    },
    resizable: {
      type: Boolean,
      default: true
    },
    tableName: {
      type: String,
      required: true
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    // 
    // truefalse,default, full, outer, inner, none
    border: {
      type: [Boolean, String],
      default: true
    },
    // 
    // truefalse
    autoResize: {
      type: Boolean,
      default: true
    },
    // 
    //  left, center, right
    headerAlign: {
      type: String,
      default: 'center'
    },
    // 
    // truefalse
    showFooter: {
      type: Boolean,
      default: false
    },
    // 
    // ellipsis,title title,tooltip tooltip 
    showFooterOverflow: {
      type: [Boolean, String],
      default: 'tooltip'
    },
    // 
    showOverflow: {
      type: [Boolean, String],
      default: 'tooltip'
    },
    // 
    // truefalse
    stripe: {
      type: Boolean,
      default: true
    },
    // 
    // %px
    maxHeight: {
      type: [Number, String],
      default: ''
    },
    height: {
      type: [Number, String],
      default: '500px'
    },
    // 
    // 
    size: {
      type: String,
      default: 'small'
    },
    // 
    // truefalse
    highlightHoverRow: {
      type: Boolean,
      default: true
    },
    // 
    // truefalse
    highlightCurrentRow: {
      type: Boolean,
      default: false
    },
    // 
    // truefalse
    tableLoading: {
      type: Boolean,
      default: false
    },
    // tooltip 
    // theme()darklight;enterable( tooltip );leaveDelay( tooltip)
    tooltipConfig: {
      type: Object,
      default: () => {
        return {
          theme: 'light',
          enterable: true
        };
      }
    },
    checkboxConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    exportConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    // 
    // truefalse
    keepSource: {
      type: Boolean,
      default: false
    },
    // (keepSource)
    editConfig: {
      type: [Boolean, Object],
      default: false
    },
    // 
    tableDatas: {
      type: Array,
      required: true
    },
    // // 
    // mergeArr: {
    //   type: Array,
    //   default: () => {
    //     return [];
    //   }
    // },
    // 
    tableColumns: {
      type: Array,
      required: true
    },
    // 
    tablePage: {
      type: Object,
      required: true
    },
    checkRecords: {
      type: Array,
      default: () => {
        return [];
      }
    }
    /* ,
    isAllChecked: {
      type: Boolean,
      default: false
    },
    isIndeterminate: {
      type: Boolean,
      default: false
    },
    selectRecords: {
      type: Object,
      default: () => {
        return {};
      }
    } */
  },
  data() {
    return {
      isAllChecked: false,
      isIndeterminate: false,
      selectRecords: [],
      mergeArr: []
    };
  },
  watch: {
    checkRecords(val, oldVal) {
      if (val.length === 0) {
        this.checkboxChangeEvent([]);
      }
    }
  },
  methods: {
    handleCellRender({ row, column, cellValue }) {
      // 
      console.log(666, row, column, cellValue);
      return {
        style: 'color: red;'
      };
    },
    /**
     * @description: 
     * @param {Number} currentPage 
     * @param {Number} pageSize 
     * @return:
     * @author: YZZ
     */
    handlePageChange({ currentPage, pageSize }) {
      this.isAllChecked = false;
      this.selectRecords = [];
      this.$emit('onTablePageChange', currentPage, pageSize);
    },
    /**
     * @description: 
     * @param {Object} column 
     * @param {String} property 
     * @param {String} order ascdesc
     * @return:
     * @author: YZZ
     */
    sortChange({ column, property, order }) {
      console.log(666);
      this.isAllChecked = false;
      this.selectRecords = [];
      this.$emit('sortChange', property, order);
    },
    /**
     * @description: 
     * @param {Objcet} records 
     * @return:
     * @author: YZZ
     */
    checkboxChangeEvent({ records }) {
      this.isAllChecked = this.$refs[
        this.tableName + 'Table'
      ].isAllCheckboxChecked();
      this.isIndeterminate = this.$refs[
        this.tableName + 'Table'
      ].isCheckboxIndeterminate();
      if (records) {
        this.selectRecords = records;
      } else {
        this.selectRecords = [];
        this.isIndeterminate = false;
      }
      if (this.selectRecords.length === 0) {
        this.isAllChecked = false;
      }
      this.$emit('chooseCheck', records);
    },
    /**
     * @description: 
     * @param {type}
     * @return:
     * @author: YZZ
     */
    changeAllEvent() {
      // 
      this.$refs[this.tableName + 'Table'].setAllCheckboxRow(this.isAllChecked);
      // 
      this.selectRecords = this.$refs[
        this.tableName + 'Table'
      ].getCheckboxRecords();
      if (this.selectRecords.length === 0) {
        this.isAllChecked = false;
      }
      this.$emit('chooseCheck', this.selectRecords);
    },
    /**
     * @description: 
     * @param fileName 
     * @param data 
     * @param columns 
     * @return: 
     * @author: ZJM
     */
    exportData(fileName, data, columns) {
      console.log(columns);

      const option = {
        isColgroup: true,
        filename: fileName,
        useStyle: true,
        // original: true,
        type: 'xlsx',
        // type: 'csv',
        // isHeader: true,
        // isFooter: true,
        columns: columns.filter(column => {
          console.log(column);
          // 
          return column.field !== '';
        }),
        data: data
      };
      // if (columns) {
      //   option.columnFilterMethod = (column) =>
      //     columns.includes(column.property);
      // }
      // console.log(data);
      this.$refs[this.tableName + 'Table'].exportData(option);
    },
    exportDatax(fileName, data, columns) {
      console.log(columns);

      const option = {
        isColgroup: true,
        filename: fileName,
        useStyle: true,
        // original: true,
        // type: 'xlsx',
        type: 'csv',
        // isHeader: true,
        // isFooter: true,
        // columns: columns.filter(column => {
        //   console.log(column);
        //   // 
        //   return column.field !== '';
        // }),
        data: data
      };
      if (columns) {
        option.columnFilterMethod = (column) =>
          columns.includes(column.property);
      }
      // console.log(data);
      this.$refs[this.tableName + 'Table'].exportData(option);
    },
    /**
     * @description: 
     * @return: 
     * @author: Lois
     */
    cellClickEvent({ row }) {
      this.$emit('clickRows', row);
    },
    /**
     * @description: 
     * @return: 
     * @author: Lois
     */
    cellDBLClickEvent({ row }) {
      this.$emit('dbclickRows', row);
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    currentChangeEvent({ row }) {
      const index = this.$refs[this.tableName + 'Table'].$getRowIndex(row);
      this.$emit('currentChangeEvent', row, index);
    },
    /**
     * @description: 
     * @param index 
     * @return: 
     * @author: ZJM
     */
    setCurrentRow(index) {
      this.$refs[this.tableName + 'Table'].setCurrentRow(
        this.tableDatas[index]
      );
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    clearCurrentRow() {
      this.$refs[this.tableName + 'Table'].clearCurrentRow();
    },
    /**
     * @description: checkbox
     * @param 
     * @return: 
     * @author: ZJM
     */
    toggleCheckboxRow(row) {
      this.$refs[this.tableName + 'Table'].toggleCheckboxRow(row);
    },
    /**
     * @description: checkbox
     * @param 
     * @return: 
     * @author: ZJM
     */
    clearCheckboxRow() {
      this.$refs[this.tableName + 'Table'].clearCheckboxRow();
    },
    /**
     * @description: checkbox
     * @param 
     * @return: 
     * @author: ZJM
     */
    getCheckboxRecords() {
      return this.$refs[this.tableName + 'Table'].getCheckboxRecords();
    },
    /**
     * @description: radio
     * @param 
     * @return: 
     * @author: ZJM
     */
    getRadioRecord() {
      return this.$refs[this.tableName + 'Table'].getRadioRecord();
    },
    setCheckboxRow(row, chekcked) {
      return this.$refs[this.tableName + 'Table'].setCheckboxRow(row, chekcked);
    },
    mergeRowMethod({ row, $rowIndex, column, data }) {
      const fields = this.mergeArr;
      if (this.mergeArr.length > 0) {
        var cellValue = row[column.property];
        if (cellValue && fields.includes(column.property)) {
          var firstCellValue = row[fields[0]];
          var prevRow = data[$rowIndex - 1];
          let nextRow = data[$rowIndex + 1];
          if (prevRow && prevRow[column.property] === cellValue && prevRow[fields[0]] === firstCellValue) {
            return { rowspan: 0, colspan: 0 };
          } else {
            let countRowspan = 1;
            while (nextRow && nextRow[column.property] === cellValue && nextRow[fields[0]] === firstCellValue) {
              nextRow = data[++countRowspan + $rowIndex];
            }
            if (countRowspan > 1) {
              return { rowspan: countRowspan, colspan: 1 };
            }
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
// checkbox
.page-left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  display: flex;
  align-items: center;
  // transform: translateX(-22.5%);
  // text-align: left;
  // display: flex;
  // align-items: center;
  // margin-top: 0px;
}

// checkbox
.tableAllCheck {
  width: 47px;
  display: flex;
  justify-content: center;
  border-right: 1px solid #e8eaec;
  margin-left: 2px;
}

// checkbox
.select-count {
  margin: 0 10px;
}
</style>
